import { Component, OnInit, ViewChild } from '@angular/core';
import { WordSpotterDataService } from '../services/word-spotter-data.service';
import { AudioPlayerService } from '../services/audio-player.service';
import { Spot } from '../models/spot.model';
import * as d3 from 'd3';
import { SpotTableItem } from '../spot-table/spot-table-datasource';
import { isNgTemplate } from '@angular/compiler';

@Component({
    selector: 'app-table-with-chart',
    template: `
    <app-spot-table #spotTable [data]="data" (renderItems)="onRenderItemsChange($event)"></app-spot-table>
    <app-spot-chart #spotChart [spots]="renderItems" [highlightedSpots]="spotTable" [domain]="domain"></app-spot-chart>
    `,
    styleUrls: ['./table-with-chart.component.scss']
})
export class TableWithChartComponent implements OnInit {

    // @ViewChild()

    data: Spot[] = [];
    domain: [number, number] = [0, 1];

    renderItems: Spot[];

    constructor(
        private wordSpotterDataService: WordSpotterDataService,
        private audioPlayerService: AudioPlayerService
        // private messageService

        ) {
            window['spotChart'] = this;

        }

    ngOnInit() {
        this.wordSpotterDataService.getData().subscribe(data => {
            this.data = data.map(item => ({ ...item, filtered: false })) as SpotTableItem[];

            // this.domain = d3.extent<Spot, number>(data, item => item.timeFrom);

            // messageService.showError
        });


    }

    onRenderItemsChange(items: Spot[]) {
        // console.log('render items change', items);

        setTimeout(() => { // fix: ExpressionChangedAfterItHasBeenCheckedError
            this.domain = d3.extent<Spot, number>(items, item => item.timeFrom);
            this.renderItems = items;
        }, 0);
    }

}
