import { Component, OnInit, Output, ViewChild, AfterViewInit, Input, EventEmitter, OnDestroy } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';
import { SpotReaderService } from '../services/spot-reader.service';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable } from '@angular/material/table';
import { SpotTableDataSource, SpotTableItem } from './spot-table-datasource';
import { Spot } from '../models/spot.model';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { isNullOrUndefined } from 'util';
import { distinctUntilChanged, debounceTime, takeUntil } from 'rxjs/operators';


// type FilterMode = 'filter' | 'highlight';

export interface FilterModeOption {
    value: 'filter' | 'highlight'; // replace with Enum
    viewValue: string;
}



@Component({
    selector: 'app-spot-table',
    templateUrl: './spot-table.component.html',
    styleUrls: ['./spot-table.component.scss']
})
export class SpotTableComponent implements OnInit, AfterViewInit, OnDestroy {
    @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
    @ViewChild(MatSort, { static: false }) sort: MatSort;
    @ViewChild(MatTable, { static: false }) table: MatTable<Spot>;

    loadingSubject = new BehaviorSubject<boolean>(false);
    // loading$ = loadingSubject.as

    private _unsubscribe = new Subject<void>();

    private dataSource = new SpotTableDataSource();
    // private dataSubject = new BehaviorSubject<Spot[]>([]);

    @Input()
    set data(value: SpotTableItem[]) {
        console.log('setting spot table data', value);
        this.dataSource.data = value;
    }

    @Input()
    set goToItem(value: Spot) {
        // this.dataSource.daat
    }

    @Output() focusedItem = new EventEmitter<Spot>();
    @Output() dataOnPage = new EventEmitter<Spot[]>();
    @Output() renderItems = new EventEmitter<Spot[]>();
    @Output() highlightedItems = new EventEmitter<Spot[]>(); // and visible

    controlTableFormGroup: FormGroup;

    filterModes: FilterModeOption[] = [
        { value: 'filter', viewValue: 'Filter' },
        { value: 'highlight', viewValue: 'Highlight' },
    ];


    // @Output() playAudi = new EventEmitter<string>();

    displayedColumns = [
        'index',
        'source',
        'keyword',
        'timeFrom',
        'timeTo',
        'score',
        'action'
    ];

    pageSizeOptions = [10, 50, 100, 500, 1000];

    set keywordPattern(value) {
        this.dataSource.keywordPattern = value;
    }

    set minScore(value) {
        this.dataSource.minScore = value;
    }

    constructor(fb: FormBuilder) {
        window['spotTable'] = this;

        this.controlTableFormGroup = fb.group({
            keyword: ['', []], // Validators.pattern()
            minScore: [0, [Validators.min(0), Validators.max(100)]],
            filterMode: 'highlight'
        });
    }

    stringPatternToRegExp(pattern) {
        if (pattern === '') {
            return null;
        }
        pattern = pattern.replace(/\*/g, '.*');
        pattern = `^${pattern}$`;
        return new RegExp(pattern, 'i');
    }

    ngOnInit() {


        // this.controls.get('highlight').valueChanges.pipe()
    }

    ngAfterViewInit() {
        this.dataSource.sort = this.sort;
        this.dataSource.paginator = this.paginator;
        this.table.dataSource = this.dataSource;

        this.controlTableFormGroup.get('keyword').valueChanges.pipe(
            debounceTime(300), // wait 1.5sec for the user to finish entering info before applying filter
            distinctUntilChanged(),
            takeUntil(this._unsubscribe)
        ).subscribe((value: string) => {
            // console.log(value)
            if (!isNullOrUndefined(value)) {
              // apply the filter to the data source
              value = value.trim().toLowerCase();

              this.dataSource.keywordPattern = this.stringPatternToRegExp(value);
            }
        });

        this.controlTableFormGroup.get('minScore').valueChanges.pipe(
            debounceTime(300), // wait 1.5sec for the user to finish entering info before applying filter
            distinctUntilChanged(),
            takeUntil(this._unsubscribe)
        ).subscribe((value: number) => {
            // console.log(value)
            this.dataSource.minScore = value;
        });

        this.dataSource.connect()
            .pipe(takeUntil(this._unsubscribe))
            .subscribe(this.renderItems);

        this.dataSource.connect()
            .pipe(takeUntil(this._unsubscribe))
            .subscribe(renderData => {
                if (this.dataSource.filterEnabled) {
                    const minScore = this.dataSource.minScore;
                    const data = renderData.filter(item => item.score >= minScore);
                    this.highlightedItems.next(data);
                }
            });

    }

    adjustSort(key: string) {

    }

    playAudio(index) {

    }

    onRowClicked(row) {
        console.log('Row clicked: ', row);
    }

    onFilterModeChanged(mode) {
        console.log('Filter mode changed: ', mode);
        this.dataSource.filterEnabled = (mode === 'filter');
    }

    filteredDataLength() {
        return this.dataSource.filteredData.length;
    }

    // isHighlight(item) {
    //     return item.score >= this.dataSource.minScore; // || this.dataSource.;
    // }

    trackByFn(item: Spot) {
        return item.index;
    }

    ngOnDestroy() {
        this._unsubscribe.next();
        this._unsubscribe.complete();
    }

}
