import { Injectable } from '@angular/core';
import { BehaviorSubject, throwError, Observable, of } from 'rxjs';
import { Spot } from '../models/spot.model';
import { HttpErrorResponse } from '@angular/common/http';
import { catchError, delay } from 'rxjs/operators';
import { parseSpotFileData } from '../utils/parse-spot-file-data';
import { spotMock } from '../spot-mock.data';

@Injectable({
    providedIn: 'root'
})
export class WordSpotterDataService {

    private wordspotterUrl = 'api/data';

    //private dataSubject = new BehaviorSubject<Spot[]>([]);

    constructor() { }

    getData(): Observable<Spot[]> {
        //return this.dataSubject. asObservable();

        return of(this.getMockData())
            .pipe(
                delay(500),
                catchError(this.handleError),
            );
    }

    getMockData() {
        const spots = parseSpotFileData(spotMock);
        return spots;
    }

    private handleError(err: HttpErrorResponse) {
        // in a real world app, we may send the server to some remote logging infrastructure
        // instead of just logging it to the console
        let errorMessage: string;
        if (err.error instanceof Error) {
            // A client-side or network error occurred. Handle it accordingly.
            errorMessage = `An error occurred: ${err.error.message}`;
        } else {
            // The backend returned an unsuccessful response code.
            // The response body may contain clues as to what went wrong,
            errorMessage = `Backend returned code ${err.status}, body was: ${err.error}`;
        }
        console.error(err);
        return throwError(errorMessage);
    }


}
