import { DataSource } from '@angular/cdk/collections';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { MatSort, Sort } from '@angular/material/sort';
import { map, finalize, catchError, filter } from 'rxjs/operators';
import { Observable, of as observableOf, merge, BehaviorSubject, of, combineLatest, Subject, Subscription } from 'rxjs';
import { Spot } from '../models/spot.model';

//window["of"] = observableOf;

/** Simple sort comparator for example ID/Name columns (for client-side sorting). */
function compare(a, b, isAsc) {
    return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}

function compareStrings(a, b, isAsc) {
    return a.localeCompare(b) * (isAsc ? 1 : -1);
}

// export interface DataAttributes {
//     filtered: boolean;
// }

export interface SpotTableItem extends Spot  {
    filtered: boolean;
}

export class SpotTableDataSource extends DataSource<Spot> {

    private readonly _data = new BehaviorSubject<SpotTableItem[]>([]);
    private readonly _renderData = new BehaviorSubject<SpotTableItem[]>([]);
    private readonly _filterEnabled = new BehaviorSubject<boolean>(false);
    private readonly _internalPageChanges = new Subject<void>();
    private readonly _minScore = new BehaviorSubject<number>(0);
    private readonly _keywordPattern = new BehaviorSubject<RegExp>(null);

    // private readonly _dataAttributes = new BehaviorSubject<DataAttributes[]>([]);
    // private readonly loadingSubject = new BehaviorSubject<boolean>(false);
    // loading$ = this.loadingSubject.asObservable();

    _renderChangesSubscription = Subscription.EMPTY;

    filteredData: SpotTableItem[];

    filteredDataMap = new WeakMap<Spot, any>();

    set filterEnabled(value: boolean) { this._filterEnabled.next(value); }
    get filterEnabled(): boolean { return this._filterEnabled.value; }

    get sort(): MatSort | null { return this._sort; }
    set sort(value: MatSort) {
        this._sort = value;
        this._updateChangeSubscription();
    }
    private _sort: MatSort;

    get paginator(): MatPaginator | null { return this._paginator; }
    set paginator(value: MatPaginator) {
        this._paginator = value;
        this._updateChangeSubscription();
    }
    private _paginator: MatPaginator | null;


    set data(value: SpotTableItem[]) { this._data.next(value); }
    get data(): SpotTableItem[] { return this._data.value; }

    set keywordPattern(value: RegExp) { this._keywordPattern.next(value); }
    get keywordPattern(): RegExp { return this._keywordPattern.value; }

    set minScore(value: number) { this._minScore.next(value); }
    get minScore(): number { return this._minScore.value; }


    sortData: ((data: SpotTableItem[], sort: MatSort) => SpotTableItem[]) = (data: SpotTableItem[], sort: MatSort): SpotTableItem[] => {
        const active = sort.active;
        const direction = sort.direction;
        if (!active || direction === '') { return data; }

        return data.sort((a, b) => {
            const isAsc = this.sort.direction === 'asc';
            switch (this.sort.active) {
                case 'index': return compare(a.index, b.index, isAsc);
                case 'source': return compare(a.filename, b.filename, isAsc);
                case 'score': return compare(a.score, b.score, isAsc);
                case 'timeFrom': return compare(a.timeFrom, b.timeFrom, isAsc);
                case 'timeTo': return compare(a.timeTo, b.timeTo, isAsc);
                case 'keyword': return compareStrings(a.keyword, b.keyword, isAsc);
                default: return 0;
            }
        });
    }


    constructor() {
        super();
        this._updateChangeSubscription();
    }

    _updateChangeSubscription() {
        const sortChange: Observable<Sort | null | void> = this._sort ?
            merge(this._sort.sortChange, this._sort.initialized) as Observable<Sort | void> :
            observableOf(null);

        const pageChange: Observable<PageEvent | null | void> = this._paginator ?
            merge(
                this._paginator.page,
                this._internalPageChanges,
                this._paginator.initialized
            ) as Observable<PageEvent | void> :
            observableOf(null);

        const dataStream = this._data;

        const filteredData = combineLatest(dataStream, this._minScore, this._keywordPattern, this._filterEnabled)
            .pipe(map(([data]) => this._filterData(data)));

        const orderedData = combineLatest(filteredData, sortChange)
            .pipe(map(([data]) => this._orderData(data)));

        const paginatedData = combineLatest(orderedData, pageChange)
            .pipe(map(([data]) => this._pageData(data)));

        this._renderChangesSubscription.unsubscribe();
        this._renderChangesSubscription = paginatedData.subscribe(data => this._renderData.next(data));
    }

    _filterData(data: SpotTableItem[]) {
        // this.filteredData = !this.filter ? data : data.filter(obj => this.filterPredicate(obj, this.filter));

        const minScore = this._minScore.value;
        const keywordPattern = this._keywordPattern.value;

        let filteringResult = false;

        if (!this.filterEnabled) {
            let item: any = null;
            if (minScore === 0 && keywordPattern == null) {
                for (let i = 0, I = data.length; i < I; i++) {
                    data[i].filtered = false;
                }
            } else {
                for (let i = 0, I = data.length; i < I; i++) {
                    item = data[i];
                    if (keywordPattern) {
                        filteringResult = item.score >= minScore && keywordPattern.test(item.keyword);
                        item.filtered = filteringResult;
                    } else {
                        filteringResult = item.score >= minScore;
                        item.filtered = filteringResult;
                    }
                }
            }

            this.filteredData = data;
        } else {
            this.filteredData = data.filter((item: SpotTableItem) => {
                if (keywordPattern) {
                    filteringResult = item.score >= minScore && keywordPattern.test(item.keyword);
                    item.filtered = filteringResult;
                    return filteringResult;
                } else {
                    filteringResult = item.score >= minScore;
                    item.filtered = filteringResult;
                    return filteringResult;
                }
            });
        }

        if (this.paginator) { this._updatePaginator(this.filteredData.length); }

        return this.filteredData;
    }

    /**
     * Returns a sorted copy of the data if MatSort has a sort applied, otherwise just returns the
     * data array as provided. Uses the default data accessor for data lookup, unless a
     * sortDataAccessor function is defined.
     */
    _orderData(data: SpotTableItem[]): SpotTableItem[] {
        // If there is no active sort or direction, return the data without trying to sort.
        if (!this.sort) { return data; }

        return this.sortData(data.slice(), this.sort);
    }

    /**
     * Returns a paged slice of the provided data array according to the provided MatPaginator's page
     * index and length. If there is no paginator provided, returns the data array as provided.
     */
    _pageData(data: SpotTableItem[]): SpotTableItem[] {
        if (!this.paginator) { return data; }

        const startIndex = this.paginator.pageIndex * this.paginator.pageSize;
        return data.slice(startIndex, startIndex + this.paginator.pageSize);
    }

    /**
     * Updates the paginator to reflect the length of the filtered data, and makes sure that the page
     * index does not exceed the paginator's last page. Values are changed in a resolved promise to
     * guard against making property changes within a round of change detection.
     */
    _updatePaginator(filteredDataLength: number) {
        Promise.resolve().then(() => {
            const paginator = this.paginator;

            if (!paginator) { return; }

            paginator.length = filteredDataLength;

            // If the page index is set beyond the page, reduce it to the last page.
            if (paginator.pageIndex > 0) {
                const lastPageIndex = Math.ceil(paginator.length / paginator.pageSize) - 1 || 0;
                const newPageIndex = Math.min(paginator.pageIndex, lastPageIndex);

                if (newPageIndex !== paginator.pageIndex) {
                    paginator.pageIndex = newPageIndex;

                    // Since the paginator only emits after user-generated changes,
                    // we need our own stream so we know to should re-render the data.
                    this._internalPageChanges.next();
                }
            }
        });
    }

    /**
     * Connect this data source to the table. The table will only update when
     * the returned stream emits new items.
     * @returns A stream of the items to be rendered.
     */
    connect() { return this._renderData; }

    /**
     *  Called when the table is being destroyed. Use this function, to clean up
     * any open connections or free any held resources that were set up during connect.
     */
    disconnect() {}

}

